"use server"

import { createClient } from "@/lib/supabase/server"
import { revalidatePath } from "next/cache"
import { redirect } from "next/navigation"

export async function createTransaction(formData: FormData) {
  const supabase = await createClient()

  const {
    data: { user },
  } = await supabase.auth.getUser()

  if (!user) {
    return { error: "Usuário não autenticado" }
  }

  const amount = Number.parseFloat(formData.get("amount") as string)
  const type = formData.get("type") as string
  const creditCardId = (formData.get("credit_card_id") as string) || null

  const { data, error } = await supabase.rpc("create_transaction_with_balance", {
    p_user_id: user.id,
    p_title: formData.get("title") as string,
    p_amount: amount,
    p_type: type,
    p_category_id: (formData.get("category_id") as string) || null,
    p_credit_card_id: creditCardId,
    p_date: formData.get("date") as string,
    p_description: (formData.get("description") as string) || null,
  })

  if (error) {
    return { error: error.message }
  }

  if (data && !data.success) {
    return { error: data.error }
  }

  revalidatePath("/transactions")
  revalidatePath("/dashboard")
  revalidatePath("/credit-cards")
  redirect("/transactions")
}

